
import React, { useState, useEffect } from 'react'
import hint from './img/hint.svg'
import skip from './img/skip.svg'
import axios from 'axios'
import closeModalIcon from '../../assets/img/close-modal-icon.svg'
import { setCookie, getCookie, eqInNumber } from '../../utils/utils'
import apiProxy from '../../model/ApiProxy'
import './hint.less'
import { DeriEnv } from '../../lib/web3js/shared/config/env';
import { useWallet } from 'use-wallet'
export default function Announcement({ router }) {
  const [show, setShow] = useState(false)
  const [isSkip, setIsSkip] = useState(true)
  const [margin, setMargin] = useState(0)
  const [isVip, setIsVip] = useState(false)
  const wallet = useWallet()
  const closeHint = () => {
    if (isSkip) {
      setCookie('trade-aribitrum-hint', "false", 1)
    }
    setShow(false)
  }

  const getRetiredPoolsInfo = async () => {
    let res = await apiProxy.request('getRetiredPoolsInfo', ['56', wallet.account])
    if (res.length) {
      setMargin(res[0].dynamicEffectiveBalance)
    }

  }

  useEffect(() => {
    if (+margin > 0) {
      let cookie = getCookie('close-deri-hint')
      if (cookie === 'false') {
        setIsVip(false)
      } else {
        setIsVip(true)
      }
    }

  }, [margin])

  useEffect(() => {
    //timestep 1661972400
    let timestamp = new Date()
    if(timestamp.getTime() >= 1661972400000){
      return false;
    }
    let sess = getCookie('trade-aribitrum-hint')
    if (sess === "false") {
      return false;
    }
    setShow(true)
  }, [])

  useEffect(() => {
    if (wallet.isConnected() && eqInNumber(wallet.chainId, 56) && router === "trade") {
      getRetiredPoolsInfo()
    }
  }, [wallet.status])

  return (
    DeriEnv.get() === 'prod' ? <div>
      {show && (router === "trade" || router === "pool") && <div className={isVip ? 'trade-hint  close-position-hint have-vip' : 'trade-hint  close-position-hint'}>
        <div className='hint-img'>
          <img src={hint} alt='' />
        </div>
        <div className='hint-box'>
          <div className='hint-title'>
            <span>
              Arbitrum Maintenance Notice
            </span>
            <img className='close' src={closeModalIcon} alt='X' onClick={closeHint} />
          </div>
          <div className='hint-content'>
            <div className='hint-text vip-text'>
              Deri Protocol will be under maintenance during the Arbitrum Nitro upgrade on Wednesday,31 Aug 22,2:30 PM UTC.
              <br />
              <br />
              All services on Arbitrum are not available during maintenance.
            </div>
            <div className='hint-link vip-skip'>
              <div className='learn-more'>
                <a target="_blank" rel="noreferrer" href='https://deri-protocol.medium.com/arbitrum-nitro-71361027eaad'>Learn More</a>
              </div>
              <div className={isSkip ? 'skip check-skip' : 'skip'} onClick={() => setIsSkip(!isSkip)}>
                {isSkip && <img src={skip} />}
              </div>  <span>skip notification until tomorrow</span>
            </div>
          </div>
        </div>
        <div className='m-check' onClick={closeHint}>
          <span className='check'>
            OK
          </span>
        </div>
      </div>}

    </div> : null

  )
}